.skillsHeader {
    display: flex;
    align-items: center;
    justify-content: center; 
    color:#f6bd60
}

.skillsHeader h2 {
    font-family: var(--primaryFont);
    font-style: normal;
    font-weight: bold;
    font-size: 3.5rem;
    text-align: center;
}

.skillsContainer {
    display: flex;
    align-items: center;
    justify-content: center; 
    width: 90%;
    /* padding: 0 1rem; */
}

.skill--scroll {
    width: 100%;
    /* margin: 0 2rem; */
}


.skill--box {
    background: #a71f1f;
    /* box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.12); */
    border-radius: 10px;
    width: 142px;
    height: 160px;
    /* margin: 0 1.5rem; */
    margin:0 4px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 2rem 1rem;
    transition: 300ms ease-in-out;
    color:#a71f1f;
}

.skill--box:hover  {
    transform: scale(1.15);
}


.skill--box > img {
    height: 50px;
    pointer-events: none;
}


.skill--box h3 {
    font-family: Big Shoulders Text;
    font-style: normal;
    font-weight: 500;
    font-size: 22px;
    text-align: center;
    margin-top: 0.5rem;
}


.marquee {
    padding: 3rem 0;
}


@media (min-width: 992px) and (max-width: 1380px) {
    
    .skillsContainer {
        /* padding: 1.5rem; */
        margin-top: 1.5rem;
    }
}

@media screen and (max-width: 800px) {
    
    /* .skillsContainer {
        padding: 0.5rem;
    } */
    .skillsHeader h2 {
        font-size: 3rem;
    }

    .skill--scroll {
        width: 100%;
        margin: 0;
    }
}

@media screen and (max-width: 600px) {
    .skill--box {
        width: 100px;
        /* height: 135px; */
        /* margin: 1.2rem; */
        /* padding: 1rem 1rem; */
    }

    .skill--box > img {
        height: 40px;
    }

    .skillsHeader h2 {
        font-size: 2.5rem;
    }

    .skill--box h3 {
        font-size: 18px;
        margin-top: 1rem;
    }
}
